import axios from 'axios'

export default {
  name: 'module',
  data () {
    return {
      module: [],
      mission: []
    }
  },
  methods: {
    getModule () {
      axios.get(this.url + '/module/project/' + this.$route.params.pId)
        .then(this.getModuleSucc)
    },
    getModuleSucc (res) {
      res = res.data
      if (res) {
        this.module = res
        this.mission = res.missions
        console.log(this.mission)
      }
    },
    getPace (sTime, eTime) {
      console.log(sTime + 'pacemodule')
      console.log(eTime + 'etime')
      sTime = new Date(sTime.replace(/-/g, '/'))
      eTime = new Date(eTime.replace(/-/g, '/'))
      let diff = eTime.getTime() - sTime.getTime()
      let dayDiff = Math.floor(diff / (24 * 3600 * 1000))
      let date = new Date()
      let nowDiff = date.getTime() - sTime.getTime()
      let nowDayDiff = Math.floor(nowDiff / (24 * 3600 * 1000))
      let num = (100 * nowDayDiff / dayDiff).toFixed(2)
      return parseFloat(num)
    }
  },
  created () {
    this.getModule()
  }
}



